const TableMixin = {
    props: {
        /*base table*/
        data: {type: Array, default: () => []},         //
        rowNum: {type: Number, default: 10},            //
        rowHeight: {type: Number, default: 36},         //
        headRowHeight: {type: Number, default: 40},     //
        noHeader: {type: Boolean},                      //
        /*base table*/
        tableData: {},                                  //
        headColumns: {type: Array},                     //
        bodyColumns: {type: Array},                     //
        fixedExist: {type: Object},                     //
        fixed: {type: String, default: 'center'},       //:left,center,right
        hostWidth: {type: Number},                      //
        sortField: {type: String},                      //
        sortDesc: {type: Boolean, default: true},       //
    },
}

export {
    TableMixin,
}